.App {
  text-align: center;
}

.App-logo {
  height: 60vmin;
  pointer-events: none;
}

@media only screen and (max-width: 600px) {
  .App-logo {
    height: 100vmin;
  }
}

.App-header {
  background-color: #f1f1f1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #545454;
  font-weight: bold;
}

.App-link {
  color:#f5f5f5;
  
}

.hudai_text{
  /* color:cornflowerblue */
  /* font-weight: normal; */
  color:#545454 ;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* shresthos design */
p{
  margin-top: 0px;
  padding-top: 0px;
  font-size: 35px;
  font-weight: 600;
  font-weight: bold;
  /* color: #00FFFF; */
  color: #545454;
  
}

.social-icons{
  display: inline-block;
}
.social-icons > a{
    font-size: 30px;
    padding-left: 10px;
}
.social-icons > a:hover{
  color: #00FFFF;
  color: #545454;
}
.timer{
  font-size: 25px;
  font-weight: normal;
}
.timer_stamp{
  display: inline-block;
  font-size:50%;
}